import { Injectable } from '@angular/core';
import { DomSanitizer } from "@angular/platform-browser";
import { MatIconRegistry } from "@angular/material/icon";

@Injectable({
  providedIn: 'root'
})
export class CustomIconsService {

  constructor(
    private iconRegistry: MatIconRegistry,
    private sanitizer: DomSanitizer
  ) { }

  init() {
    this.iconRegistry.addSvgIcon(
      'gmail',
      this.sanitizer.bypassSecurityTrustResourceUrl('assets/images/icons/gmail.svg'));
    
      this.iconRegistry.addSvgIcon(
        'zoomVideo',
        this.sanitizer.bypassSecurityTrustResourceUrl('assets/images/icons/zoom.svg'));

    this.iconRegistry.addSvgIcon(
      'linkedin',
      this.sanitizer.bypassSecurityTrustResourceUrl('assets/images/icons/linkedin.svg'));

    this.iconRegistry.addSvgIcon(
      'twitter',
      this.sanitizer.bypassSecurityTrustResourceUrl('assets/images/icons/twitter.svg'));

    
  }
}
